import "./content.css";
import { useState } from "react";
import React from "react";

const Content = ({ questions }) => {
    const [questionIndex, setQuestionIndex] = useState(0);
    const activeQuestion = questions[questionIndex];

    return (
        <main>
            <fieldset>
                <h3 id="group_label_1">{activeQuestion.question.romana}</h3>

                {questions.map((q) => (
                    <label
                        role="radio"
                        aria-checked="false"
                        key={q.id}
                        name="rd1"
                    >
                        {q.answer}
                        <input
                            type="radio"
                            aria-checked="false"
                            name="rd1"
                        ></input>
                    </label>
                ))}
            </fieldset>
        </main>
    );
};

export default Content;
